import React from "react";
import ParticleImage, { Vector, forces } from "react-particle-image";

const particleOptions = {
  filter: ({ x, y, image }) => {
    // Get pixel
    const pixel = image.get(x, y);
    // Make a particle for this pixel if blue > 50 (range 0-255)
    return pixel.b > 20;
  },
  color: ({ x, y, image }) => "rgba(85, 107, 47)",
  radius: () => Math.random() * 0.25 + 0.25,
  mass: () => 20,
  friction: () => 0.25,
  initialPosition: ({ canvasDimensions }) => {
    return new Vector(canvasDimensions.width / 4, canvasDimensions.height / 4);
  }
};

const motionForce = (x, y) => {
  return forces.disturbance(x, y, 3);
};

const Logo = () => {
  return (
    <ParticleImage
      src={"/icon-only.png"}
      scale={0.35}
      entropy={2}
      maxParticles={4000}
      mouseMoveForce={motionForce}
      touchMoveForce={motionForce}
      particleOptions={particleOptions}
      backgroundColor="transparent"
    />
  );
};

export default Logo;
